.empty__cart {
  position: relative;
  width: 100%;
  height: 309px;
  top: 140px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.empty__cart__text {
  color: #757575;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 27px;
}

.empty__cart__button {
  margin: 0;
  height: 48px;
  padding: 0 36px;
  font-size: 14px;
  line-height: 46px;
  border-width: 1px;
  color: #f57224;
  border-style: solid;
  background-color: transparent;
  border-color: #f57224;
}

.empty__cart__button:hover {
  background-color: rgba(245, 113, 36, 0.164);
  cursor: pointer;
}

.cart {
  max-width: 1188px;
  position: relative;
  margin: auto;
}

.cart__container {
  position: relative;
  width: 100%;
  top: 132px;
  background-color: #f4f4f4;
  margin: 0 auto;
  margin-bottom: 20px;
}
/* products container */
.cart__container__left {
  width: 788px;
  float: left;
}

.cart__container__header {
  background-color: #fff;
  margin-bottom: 12px;
}

.header__container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 12px;
  line-height: 14px;
  color: #757575;
}

.header__container__main {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  line-height: 40px;
  padding-left: 12px;
  padding-right: 12px;
  text-transform: uppercase;
}

.header__container__main__checkbox {
  display: flex;
  align-items: center;
}

.header__container__main__operations:hover {
  cursor: pointer;
  color: #f57124b7;
}

.delete__button__container {
  height: 100%;
  display: flex;
  align-items: center;
}

.delete__button__container :first-child {
  position: relative;
  top: 3px;
}

.cart__container__shop {
  background-color: #fff;
  margin-bottom: 12px;
  position: relative;
}

.cart__container__shop__title {
  position: sticky;
  width: 100%;
  top: 0;
  z-index: 2;
  background-color: #fff;
}

.checkout__shop {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 36px;
  line-height: 40px;
  padding: 0 12px;
  background-color: #fff;
}

.checkout__shop__left {
  display: flex;
  align-items: center;
}

.checkout__shop__left > input {
  margin-right: 16px;
  position: relative;
  width: 16px;
  height: 16px;
  line-height: 14px;
}

.checkout__shop__title {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #212121;
  margin-right: 8px;
}
.checkout__shop__title > span {
  cursor: pointer;
}

.checkout__shop__title > span:hover {
  color: #50b8e3;
}
.checkout__shop__title :nth-child(2) {
  color: #757575;
}

.checkout__shop__right {
  font-size: 14px;
  color: #ff330c;
  margin-right: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.checkout__shop__right :nth-child(2) {
  transform: rotate(90deg);
}

.checkout__shop__progress {
  height: 3px;
  background: #efefef;
  border-radius: 8px;
  margin: 0 12px 7px 44px;
}

.checkout__shop__delivery {
  display: flex;
  margin: 4px 12px 6px 44px;
}

.checkout__shop__delivery__fee {
  color: rgb(0, 0, 0);
  font-size: 12px;
  font-weight: normal;
}

.cart__item {
  width: 100%;
  padding: 16px 0;
  background-color: #fff;
  font-size: 12px;
  font-weight: 400;
  text-align: left;
}

.cart__item__inner {
  width: 100%;
  display: table;
}

.cart__item__left {
  display: table-cell;
  vertical-align: top;
  width: calc(100% - 156px - 139px);
  padding-left: 10px;
}
.cart__item__left > label > input {
  float: left;
  margin-right: 16px;
  margin-top: 32px;
  position: relative;
  width: 16px;
  height: 16px;
  line-height: 14px;
}

.cart__image {
  position: relative;
  float: left;
  width: 80px;
  height: 80px;
  margin-right: 12px;
}

.cart__image img {
  width: 80px;
  height: 80px;
}

.cart__content {
  display: inline-block;
  width: 328px;
  position: relative;
}

.cart__content :first-child {
  display: block;
  width: 100%;
  max-height: 32px;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-left: 0;
  font-size: 14px;
  color: #212121;
  line-height: 16px;
}

.cart__content :nth-child(2) {
  display: block;
  margin-top: 6px;
  font-size: 12px;
  line-height: 16px;
  color: #757575;
}

.cart__item__middle {
  display: table-cell;
  vertical-align: top;
  width: 156px;
  padding-left: 9px;
}

.cart__item__middle1 {
  font-size: 18px;
  line-height: 28px;
  color: #f57224;
  font-weight: 500;
  margin-bottom: 4px;
}

.cart__item__middle2 {
  font-size: 14px;
  line-height: 16px;
  color: #757575;
  margin-bottom: 8px;
  text-decoration: line-through;
}

.cart__item__middle3 {
  font-size: 14px;
  line-height: 16px;
  color: #212121;
  font-weight: 500;
}

.cart__item__middle__operations {
  margin-top: 8px;
  color: #9e9e9e;
}

.cart__item__middle__operations > span {
  cursor: pointer;
  font-size: 24px;
  margin-right: 8px;
}

.cart__item__right {
  display: table-cell;
  padding-right: 40px;
  width: 139px;
  text-align: right;
}

.item__quantity {
  text-align: right;
  vertical-align: middle;
  font-size: 16px;
  line-height: 28px;
  color: #212121;
}

.item__quantity__picker {
  color: #212121;
  font-family: Roboto, Helvetica Neue, Helvetica, Tahoma, Arial, Sans-serif;
  font-size: 14px;
  line-height: 1.28571;
  display: inline-block;
  margin: 0;
  padding: 0;
  vertical-align: middle;
}

.item__quantity__picker > div {
  width: 108px;
  margin-left: 0;
  display: flex;
  align-items: center;
}

.item__quantity__picker > div > button {
  width: 32px;
  height: 32px;
  text-align: center;
  cursor: pointer;
  line-height: 0;
  box-sizing: border-box;
  position: relative;
  transition: all 0.2s;
  border: 4px solid transparent;
  color: #9e9e9e;
  background-color: #eff0f5;
  font-size: 1.5rem;
}

.item__quantity__picker .add:hover {
  background-color: #c0c0c0;
  color: #fff;
}

.item__quantity__picker > div > input {
  border: transparent;
  line-height: 22px;
  height: 30px;
  width: 42px;
  text-align: center;
  transition: 0.3s;
}
.item__quantity__picker > div > input:hover {
  border: 1px solid #c0c0c0;
}

.item__quantity__picker > div > input:focus {
  outline: none !important;
  border: 1px solid rgb(54, 148, 255);
}

.cart__container__right {
  width: 388px;
  float: right;
  background-color: #f4f4f4;
  margin: 0 auto;
  margin-bottom: 20px;
}

.summary__section {
  padding: 16px 12px 0;
  background: #fff;
}
.summary__section__content {
  border-bottom: 1px solid #eff0f5;
  padding-bottom: 16px;
}
.checkout__location {
  background: #fff;
  padding: 0;
}

.location__label {
  font-size: 14px;
  color: #9e9e9e;
  letter-spacing: 0;
  line-height: 18px;
  margin-bottom: 11px;
}
.location__body {
  display: table;
  width: 100%;
  align-items: center;
}

.location__icon {
  padding-right: 10px;
  color: #9c9c9c;
}

.location__address {
  display: table-cell;
  vertical-align: middle;
  color: #202020;
  padding-right: 15px;
}

.summary__section__heading {
  font-size: 18px;
  color: #212121;
  margin-bottom: 14px;
  position: relative;
  font-weight: 500;
}

.checkout__summary__rows {
  margin-bottom: 15px;
}

.checkout__summary__row {
  display: flex;
  width: 100%;
  margin-bottom: 16px;
  justify-content: space-between;
}

.checkout__summary__label {
  font-size: 14px;
  color: #757575;
  letter-spacing: 0;
  line-height: 21px;
}
.checkout__summary__value {
  font-size: 16px;
  line-height: 21px;
  text-align: right;
  color: #202020;
  letter-spacing: -0.44px;
  vertical-align: middle;
}

.voucher__input {
  margin-bottom: 8px;
}

.voucher__input__inner {
  display: flex;
  width: 100%;
  margin-bottom: 4px;
}
.voucher__input__col {
  padding-right: 8px;
  flex: 1;
}

.voucher__input__col > input {
  color: #212121;
  height: 38px;
  width: 250px;
  padding: 0 8px;
  font-size: 14px;
  border-radius: 0;
  border: 1px solid #dadada;
}

.voucher__input__button {
  width: 104px;
}

.voucher__input__button > button {
  border: none;
  height: auto;
  line-height: 40px;
  width: 104px;
  display: block;
  background-color: #1a9cb7;
  color: #fff;
  font-size: 14px;
  padding: 0;
  border-radius: 2px;
}

.voucher__input__button > button:hover {
  cursor: pointer;
  opacity: 0.8;
  transition: all 0.3s ease-out;
}

.checkout__order__total {
  margin-top: 10px;
}

.checkout__order__total__row {
  display: table;
  width: 100%;
  margin-bottom: 16px;
}

.checkout__order__total__title {
  display: table-cell;
  font-size: 14px;
  color: #202020;
  line-height: 16px;
}

.checkout__order__total__fee {
  display: table-cell;
  font-size: 18px;
  color: #f57224;
  text-align: right;
}

.checkout__order__total__fee > small {
  font-size: 12px;
  color: #424242;
  letter-spacing: 0;
  line-height: 16px;
  display: block;
  text-align: right;
  margin-top: 5px;
}

.checkout__order__total > button {
  width: 100%;
  height: 40px;
  float: right;
  line-height: 14px;
  margin-left: 12px;
  border-style: solid;
  background-color: #f57224;
  border-color: transparent;
  color: #fff;
  transition: all 0.3s ease-out;
  box-shadow: none;
  border-radius: 2px;
}

.checkout__order__total > button:hover {
  background-color: #db5b11;
  cursor: pointer;
}
