.nav * {
  box-sizing: content-box;
}

.nav__header {
  display: block;
  position: fixed;
  z-index: 999;
  height: 115px;
  width: 100%;
  top: 0;
  background-color: #fff;
}

.header__links__bar {
  background: rgba(0, 0, 0, 0.03);
  min-width: 1006px;
  height: 25px;
  text-align: right;
  transition: height 0.25s linear, zIndex 0s linear 0.25s;
  z-index: 310;
}

.header__links__bar__content {
  width: 1188px;
  min-width: 1188px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.top__links {
  position: relative;
  display: inline-block;
  font-size: 12px;
  height: 25px;
  line-height: 25px;
  text-align: left;
  white-space: nowrap;
  cursor: pointer;
  color: #0f1568;
  transition: color 0.4s;
  padding: 0 17px;
}

.top__links > a {
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.4s;
  color: #9e9e9e;
}

.cyan {
  color: #0f1568 !important;
}

.top__links > *:hover {
  color: #f57224 !important;
  transition: color 0.2s;
}

.header__logo__bar {
  height: 75px;
  box-sizing: border-box;
  padding-top: 11px;
}

.header__logo__bar__content {
  position: relative;
  display: flex;
  align-items: center;
  width: 1188px;
  min-width: 1188px;
  margin: 0 auto;
}

.header__logo {
  display: inline-block;
  width: 200px;
  height: 39px;
}

.header__logo a {
  float: left;
}

.header__logo img {
  width: 127px;
  height: 40px;
}

.header__search * {
  box-sizing: border-box;
}

.header__search {
  width: 688px;
  display: inline-block;
  height: 45px;
  line-height: 45px;
  background-color: #fff;
  border-radius: 3px;
}

.searchbox {
  position: relative;
  width: 100%;
  height: 45px;
}

.searchbox__bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 45px;
}

.searchbox__input {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 13px 19px;
  font-family: Roboto-Regular, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 1;
  letter-spacing: 0;
  color: #212121;
  border: none;
  outline: none;
  background: #eff0f5;
  box-shadow: none;
  border-radius: 2px;
}
.searchbox__search {
  position: absolute;
  right: 0;
  top: 0;
  width: 45px;
  line-height: 1;
}

.searchbox__button {
  width: 45px;
  height: 45px;
  border: none;
  border-radius: 0 2px 2px 0;
  outline: none;
  background-size: 22px;
  box-shadow: none;
  cursor: pointer;
  background: #f57224;
}

.search {
  color: #fff;
}

.header__cart {
  display: inline-block;
  height: 38px;
  width: 40px;
  position: relative;
  margin: 0 0 0 20px;
}

.cart__icon {
  position: absolute;
  background-image: url(//laz-img-cdn.alicdn.com/tfs/TB1xEeTdBGw3KVjSZFDXXXWEpXa-75-66.png);
  background-size: cover;
  width: 29px;
  height: 26px;
  left: 50%;
  top: 50%;
  margin: -12px 0 0 -14px;
}

.cart__num {
  position: absolute;
  background: #f36e36;
  color: #fff;
  top: -4px;
  font-weight: 400;
  right: -4px;
  text-align: center;
  border: 3px solid #f36e36;
  font-size: 13px;
  min-width: 14px;
  line-height: 14px;
  border-radius: 50%;
}

.header__banner {
  width: 188px;
  height: 45px;
  display: inline-block;
  position: absolute;
  right: 0;
  top: -15px;
}

.header__banner img {
  width: 188px;
  height: 45px;
  display: block;
}
