.Slider {
  width: 988px;
  height: 344px;
  margin-left: 200px;
}

.Slider div > img {
  height: 344px;
}

.slick-dots {
  left: -100px;
  bottom: 20px;
}

.slick-dots li {
  margin: 0;
}

.slick-dots li button:before {
  font-size: 12px;
  color: #ddd;
  opacity: 0.75;
}

.slick-dots li.slick-active button:before {
  color: #fff;
  opacity: 1;
}
