.login {
  position: relative;
  top: 140px;
  min-height: 315px;
  width: 100%;
}

.login__title {
  display: flex;
  justify-content: space-between;
  width: 810px;
  margin: 50px auto 15px;
  height: 26px;
  line-height: 26px;
}

.login__title > h3 {
  font-size: 22px;
  color: #424242;
  font-weight: 400;
}

.login__form__body {
  width: 810px;
  height: 264px;
  box-sizing: border-box;
  margin: 30px auto;
  background-color: #fff;
  color: #757575;
  padding: 25px 50px 50px 25px;
}

.login__input {
  width: 380px;
  float: left;
}

.login__input__name {
  display: flex;
  flex-direction: column;
}

.login__input__label {
  font-size: 12px;
  height: 15px;
  line-height: 15px;
  overflow: hidden;
  color: #424242;
  display: block;
  margin-bottom: 5px;
}

.login__input__input {
  box-sizing: border-box;
  width: 100%;
  border: 1px solid #ddd;
  font-size: 14px;
  height: 40px;
  padding: 0 35px 0 10px;
  margin-bottom: 2px;
  outline: none;
}

.login__forgot {
  margin-top: -7px;
  text-align: right;
  font-size: 12px;
}

.login__forgot > a {
  color: #049cb9 !important;
}

.login__buttons {
  position: relative;
  top: -29px;
  width: 305px;
  float: right;
}

.login__button {
  margin: 12px 0;
}

.login__button > div {
  margin: 10px 0;
  color: #757575 !important;
}

.login__third__party__line {
  margin-bottom: 12px;
}

.login__fb {
  background: #3b5998;
  height: 40px;
  line-height: 40px;
  border-radius: 3px;
  margin-bottom: 12px;
  text-align: center;
  color: #fff;
  font-size: 18px;
  width: 100%;
  border: 0;
  outline: 0;
}

.login__google {
  width: 100%;
  background: #d34836;
  height: 40px;
  line-height: 40px;
  border-radius: 3px;
  text-align: center;
  color: #fff;
  font-size: 18px;
  border: 0;
  outline: 0;
}

button {
  cursor: pointer;
}

.login__title > div > span {
  color: #50b8e3;
}
