.header__category {
  top: 100px;
  position: fixed;
  height: 40px;
  width: 100%;
  z-index: 22220;
  background-color: #ffffff;
}

.header__category__bar {
  display: flex;
  width: 1188px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}
.header__category__dropdown:hover {
  color: #f57224;
  cursor: pointer;
}

.header__category__dropdown:hover .arrow {
  transform: rotate(270deg);
}

.header__category__dropdown:hover .nav__menu__category {
  display: block;
}

.header__category__dropdown {
  opacity: 0.85;
  font-weight: 550;
  margin-left: 20px;
  font-size: 14px;
  color: #0f136d;
  letter-spacing: 0;
  width: 180px;
  line-height: 40px;
  display: flex;
  align-items: center;
}

.header__category__dropdown .arrow {
  margin-left: 4px;
  transform: rotate(90deg);
}

.header__category__label {
  list-style: none;
  overflow: hidden;
  height: 100%;
  padding: 0;
  margin: 0;
}

.header__category__labels > a {
  display: flex;
  margin-left: 10px;
  padding-right: 10px;
  display: inline-block;
  cursor: pointer;
  height: 40px;
  text-decoration: none;
}

.header__category__labels > a > img {
  width: 21px;
  height: 21px;
  position: relative;
  top: 7px;
}

.header__category__labels > a > span {
  display: inline-block;
  line-height: 40px;
  vertical-align: middle;
  height: 40px;
  font-size: 14px;
  color: #0f136d;
}

/* SidenavCategory */

.nav__menu__category2 {
  display: none;
  left: -120px;
  top: 40px;
}
