.page {
  position: relative;
  margin: 0 auto;
  background-size: 1920px;
  background-position-x: center;
  max-width: 1188px;
  top: 460px;
}
/* channels */
.page__channels {
  margin-top: 12px;
  padding-top: 0 !important;
}

.page__channels__content {
  height: 40px;
  display: flex;
}

.page__channels__content > div {
  width: 33.33%;
  height: 40px;
  background: #fff;
  margin-right: 12px;
  border-radius: 20px;
}

.page__channels__content > div > a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  color: #000;
  font-size: 18px;
  border-radius: 20px;
}

.page__channels__content > div > a > div:last-child {
  display: none;
}

.page__channels__content > div > a > div {
  display: flex;
  align-items: center;
}

.page__channels__content > div > a > div > img {
  width: 32px;
  height: 32px;
  margin: 4px;
}

.channel__icon {
  font-size: 1.8rem !important;
  margin-top: 2px;
}

.page__channels__content > div .channel__1:hover {
  color: #50b8e3;
}

.page__channels__content > div .channel__2:hover {
  color: #ce0909;
}

.page__channels__content > div .channel__3:hover {
  color: #f5a623;
}

.page__channels__content > div > a:hover {
  box-shadow: 0px 1px 2px 0.5px rgba(95, 95, 95, 0.75);
}

.page__channels__content > div > a:hover div:last-child {
  display: block;
}
/* flash sale */

.page__flash {
  display: block;
  clear: both;
  width: 1188px;
  padding-top: 24px;
}

.flash__title {
  height: 38px;
  line-height: 38px;
}

.flash__title > h3 {
  float: left;
  font-size: 22px;
  color: #424242;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 200;
}

.flash__content {
  margin-bottom: 12px;
  background-color: #fff;
  height: 375px;
}

.flash__content__header {
  overflow: hidden;
  border-bottom: #d5d5d5 1px solid;
  height: 60px;
  line-height: 60px;
  margin-bottom: 10px;
}

.flash__content__header__text {
  float: left;
  margin-left: 20px;
}

.flash__content__header__timer {
  float: left;
  display: flex;
  overflow: hidden;
  margin-left: 65px;
}

.flash__content__header .flash__content__header__text {
  font-weight: 500;
  font-size: 14px;
  color: #f57224;
}

.flash__content__header__timer {
  font-size: 14px;
  margin-right: 10px;
  color: #202020;
}

.flash__timer {
  display: flex;
  margin-left: 5px;
}

.flash__timer > section {
  border-radius: 2px;
  font-weight: 500;
  margin: 12px 6px;
  font-size: 16px;
  color: #fff;
  background-color: #d3232a;
  text-align: center;
  width: 40px;
  height: 35px;
  line-height: 35px;
}

.flash__content__header__button {
  font-weight: 500;
  font-size: 14px;
  float: right;
  display: block;
  border: 1px solid #f57224;
  border-radius: 2px;
  color: #f57224;
  height: 35px;
  background-color: transparent;
  line-height: 35px;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
  margin-right: 11px;
  margin-top: 13px;
  cursor: pointer;
}

/* products */

.flash__content__body > div {
  display: flex;
}

.flash__content__body > div > div > a {
  display: flex;
  height: 290px;
  width: 186px;
  float: left;
  margin-right: 12px;
  position: relative;
  color: #000;
  text-decoration: none;
}

.flash__content__body > div > div > a:hover {
  box-shadow: 0px 0.5px 3px 1px rgba(156, 156, 156, 0.63);
}

.flash__content__body > div > div > a img {
  width: 186px;
  height: auto;
}

.flash__content__body .product__title {
  height: 36px;
  line-height: 18px;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin-bottom: 4px !important;
  font-size: 14px;
  margin: 0 8px;
}

.flash__content__body .product__price {
  margin-bottom: 4px !important;
  font-size: 18px;
  color: #f57224;
  letter-spacing: -0.56px;
  line-height: 22px;
  height: 22px;
  margin: 0 8px;
}

.flash__content__body .product__discount {
  font-size: 12px;
  line-height: 12px;
  margin-bottom: 4px;
  margin: 0 8px;
}

.flash__content__body .product__discount :first-child {
  color: #9e9e9e;
  text-decoration: line-through;
}

.flash__content__body .product__button {
  margin-top: 5px;
  margin-bottom: 100px;
  width: 186px;
  border: 1px solid #f57224;
  background: #f57224;
  color: #fff;
  cursor: pointer;
  border-radius: 2px;
}
.flash__content__body .product__button:hover {
  background-color: #db5b11;
  transition: all 0.3s ease-out;
}
