.nav__menu__category {
  position: relative;
  width: 200px;
  line-height: 40px;
  float: left;
  z-index: 210;
  height: 100%;
}

.nav__menu__area {
  width: 200px;
  height: 344px;
  position: absolute;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.25);
  background-color: #fff;
  z-index: 200;
}
.nav__menu__area__list * {
  list-style: none;
}

.nav__menu__area__list li {
  display: flex;
  justify-content: space-between;
}

.nav__menu__area__list a {
  padding-left: 8px;
  height: 27px;
  line-height: 27px;
  text-decoration: none;
  color: #757575;
  font-size: 13px;
  width: 100%;
}
.nav__menu__area__list a > div {
  display: flex;
  justify-content: space-between;
}

.nav__menu__area__list a > div :nth-child(2) {
  display: none;
}

.nav__menu__area__list li:hover > a {
  color: #f36d00;
  background-color: #e7e8ec;
}

.nav__menu__area__list a:hover > div :nth-child(2) {
  display: block;
}

.nav__menu__area__list {
  padding: 8px 0 12px;
  height: 100%;
  margin: 0;
}

.nav__menu__area__list li ul,
.nav__menu__area__list li ul li ul {
  opacity: 0;
  visibility: hidden;
  z-index: 999;
  left: 280px;
  top: 0;
  position: absolute;
  min-width: 200px;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.25);
  background-color: #fff;
  height: 324.5px;
  padding: 8px 0 12px;
}

.nav__menu__area__list li:hover > ul,
.nav__menu__area__list li ul li:hover > ul {
  display: block;
  opacity: 1;
  visibility: visible;
  left: 200px;
}
