* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
}

*,
li,
ol,
ul {
  box-sizing: border-box;
}

body {
  font-family: Roboto-Regular, "Helvetica Neue", Helvetica, Tahoma, Arial,
    Sans-serif;
  background-color: #f5f5f5;
  font-size: 12px;
  color: #9e9e9e;
}

a,
html {
  -webkit-tap-highlight-color: transparent;
}

.warning__span {
  min-height: 16px;
  line-height: 16px;
  font-size: 12px;
  color: #f44336;
  display: block;
  margin-bottom: 10px;
}

input[type="checkbox"] {
  filter: hue-rotate(150deg);
  margin-right: 16px;
  top: 0;
  left: 0;
  width: 16px;
  height: 16px;
}
input[type="checkbox"]:hover {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  appearance: none;
  outline: 1px solid #24cbf5;
  box-shadow: none;
  font-size: 0.8em;
  text-align: center;
  line-height: 1em;
  background: #245cf52a;
  cursor: default;
  transition: 0.3s;
}

.sign__logn__in__btn {
  width: 100%;
  color: #fff;
  height: 48px;
  padding: 0 36px;
  font-size: 14px;
  line-height: 46px;
  border-width: 1px;
  border-style: solid;
  background-color: #f57224;
  border-color: transparent;
  border-radius: 2px;
}
