.register__container {
  top: 140px;
  position: relative;
  min-height: 475px;
}

.register__title {
  width: 810px;
  margin: 50px auto 15px;
  height: 26px;
  line-height: 26px;
  display: flex;
  justify-content: space-between;
}

.register__title > h3 {
  font-size: 22px;
  color: #424242;
  font-weight: 400;
}
.register__title > div > span {
  color: #049cb9;
}

.register__form__body {
  position: relative;
  padding: 25px 50px 50px 25px;
  width: 810px;
  box-sizing: border-box;
  margin: 30px auto;
  background-color: #fff;
  height: 435px;
}

.register__form__body__col1 {
  width: 380px;
  float: left;
}
.register__form__body__col2 {
  color: #757575;

  width: 305px;
  float: right;
}
.register__phone > label,
.register__password > label,
.register__name > label {
  font-size: 12px;
  height: 15px;
  line-height: 15px;
  overflow: hidden;
  color: #424242;
  display: block;
  margin-bottom: 5px;
}
.register__phone > input,
.register__password > input,
.register__name > input {
  box-sizing: border-box;
  width: 100%;
  border: 1px solid #ddd;
  font-size: 14px;
  height: 40px;
  padding: 0 35px 0 10px;
  margin-bottom: 2px;
  outline: none;
}

.phone__sms {
  margin-bottom: 20px;
  height: 40px;
  background: #d4f3eb;
  border-radius: 6px;
}

.register__password {
  position: relative;
}

.register__info {
  display: flex;
  color: #000;
}

.register__info__bday__input {
  display: flex;
}

.input__month {
  width: 120px;
}

.register__promotions {
  display: flex;
}

.register__button {
  margin: 12px 0;
}

.register__policy {
  font-size: 12px;
  margin-bottom: 20px;
}

/* .register__policy > span {
  display: flex;
} */

a {
  color: #049cb9;
  padding-left: 3px;
}

.register__change__btn > a > button {
  border-style: solid;
  background-color: transparent;
  border-color: #f57224;
  color: #f57224;
}

.register__change__btn {
  margin-top: 12px;
}

.register__third {
  margin-top: 12px;
  display: flex;
}

.register__third__fb,
.register__third__google {
  margin-right: 4px;
  height: 40px;
  line-height: 40px;
  border-radius: 3px;
  color: #fff;
  font-size: 18px;
  border: 0;
  outline: 0;
  flex: 1;
}

.register__third__fb {
  background: #3b5998;
}
.register__third__google {
  background: #d34836;
}

.register__form__body > div > div > span {
  display: flex;
}
